.container {
  z-index: 1;
  position: fixed;
  background-color: black;
  width: 100vw;
  height: 100%;
  top: 0;
  opacity: 0.9;
  display: flex;
}

.form {
  margin: auto;
  background-color: white;
  padding: 20px;
  width: 35vw;
  border-radius: 15px;
}

.form_control {
  background-color: black;
  color: white;
  border-width: 0px;
  border-radius: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 80%;
}

.message {
  background-color: black;
  color: white;
  border-width: 0px;
  border-radius: 10px;
  width: 80%;
  margin-top: 5px;
  margin-bottom: 5px;
  resize: none;
}

.submit_btn {
  background-color: black;
  color: white;
  border-width: 0px;
  border-radius: 10px;
  padding: 5px;
  margin-top: 10px;
}

.header {
  color: black;
  margin-bottom: 10px;
}
