.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.body {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.button {
  margin-top: 200px;
  background-color: #282c34;
  color: white;
  border-width: 1px;
  border-radius: 14px;
  border-color: white;
  border-style: solid;
  padding: 5px;
}

.button:hover {
  color: #282c34;
  background-color: white;
  transition-duration: 1s;
}
